import {
  PoChartSerie,
  PoChartType,
  PoModalAction,
  PoModalComponent,
  PoTableColumn,
  PoSelectOption,
  PoI18nService,
} from "@po-ui/ng-components";
import { Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { of, Subject, takeUntil } from "rxjs";

import { DbaccessService } from "../../service/dbaccess.service";
import { Tcgetinfo } from "../../models/tcgetinfo";
import { DbaccessLiterals } from "../../models/dbaccess-literals";
import { EnvironmentsService } from "src/app/core/services/environment/environments.service";
import { HeaderService } from "src/app/shared/components/header/services/header.service";
import { HeaderData } from "src/app/shared/components/header/models/header-data.model";

const filterItemsOfConnection = (items) =>
  items.status === "1" && items.category === "connections";
const filterItemsOfComsuption = (items) =>
  items.status === "1" &&
  (items.category == "memory" || items.category == "cpu");
const filterItemsOfOthers = (items) =>
  items.status === "1" && items.category === "latency";
const filterItemsOfGenerics = (items) =>
  items.status === "0" && items.slot >= 400;
const filterOfStatic = (items) => items.status === "0" && !(items.slot === 0);
const filterOfCheckup = (items) =>
  items.status === "0" && items.category === "checkup";
const filterDetailsOfCheckup = (items) =>
  items.status === "2" && items.category === "checkup" && items.subslot > 0;
const oSlotsWithParams = {
  11: "Connection",
  12: "Seconds",
  14: "Conection Nro",
  24: "Table",
  430: "Pings",
};

@Component({
  selector: "app-dbaccess",
  templateUrl: "./dbaccess.component.html",
  styleUrls: ["./dbaccess.component.css"],
})
export class DbaccessComponent implements OnInit, OnDestroy {
  @ViewChild(PoModalComponent, { static: true }) poModal: PoModalComponent;

  headerData: HeaderData = {
    title: "DBAccess Monitor",
    icon: "po-icon po-icon-device-database",
    routeUrl: "/dbaccess",
  };

  state: string = "loading";
  dbAccessLogoMarca = "assets/totvstec-icon.png";
  disconnectedIcon = "assets/disconnect-icon.jpg";
  environmentInUse: string = "";
  slotNumber: number;
  paramOfSlot: string = "";
  valueOfRow: any;
  columnsOfSlots: Array<PoTableColumn> = [];
  columnsOfCheckup: Array<PoTableColumn> = [];
  itemsDetailsOfCheckup: Array<Tcgetinfo>;
  itemsOfCheckup: Array<Tcgetinfo>;
  itemsOfConnections: Array<Tcgetinfo>;
  itemsOfConsumption: Array<Tcgetinfo[]>;
  itemsOfOthers: Array<Tcgetinfo[]>;
  aDetails: Array<string> = [];
  environments: Array<PoSelectOption> = [];

  modalTemplate: string = "graphic";
  modalTitle: string = "Graphic";
  modalRow: Array<string[]>;
  modalInputLabel: string = "";

  /*-- Modal dos graficos --*/
  secondaryActionLabel: string;
  titleOfGraphic: string;
  heighOfGraphic: 300;
  rangeOfGraphic: Array<PoChartSerie> = [];
  graphicConsumingChartType: PoChartType = PoChartType.Donut;
  close: PoModalAction;

  /*-- Unsubscribe --*/
  destroy$: Subject<boolean> = new Subject<boolean>();

  /* -- i18n --*/
  literals: DbaccessLiterals;

  constructor(
    private dbaccessService: DbaccessService,
    private environmentsService: EnvironmentsService,
    private i18nService: PoI18nService,
    private headerService: HeaderService
  ) {
    this.i18nService
      .getLiterals({
        context: "dbaccess",
        language: this.i18nService.getLanguage(),
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe((literals: DbaccessLiterals) => (this.literals = literals));
    this.headerService.setheaderData(this.headerData);
  }

  ngOnInit(): void {
    this.close = {
      action: () => {
        this.closeModal();
      },
      label: this.literals.poModalActionClose,
    };
    this.columnsOfCheckup = [
      {
        property: "description",
        label: this.literals.columnsOfCheckupLabelDescription,
        type: "string",
        width: "50%",
      },
      {
        property: "status",
        label: this.literals.columnsOfCheckupLabelStatus,
        type: "icon",
        width: "10%",
        sortable: false,
        icons: [
          {
            action: this.getDetailsOfCheckup.bind(this),
            icon: "po-icon-exclamation",
            tooltip: this.literals.columnsOfCheckupTooltipStatus,
            value: "0",
          },
        ],
      },
    ];

    this.columnsOfSlots = [
      {
        property: "slot",
        label: this.literals.columnsOfSlotsLabelSlot,
        type: "number",
        width: "5%",
      },
      {
        property: "description",
        label: this.literals.columnsOfSlotsLabelDescription,
        type: "string",
        width: "45%",
      },
      {
        property: "value",
        label: this.literals.columnsOfSlotsLabelValue,
        type: "string",
        width: "40%",
      },
      {
        property: "param",
        label: this.literals.columnsOfSlotsLabelParam,
        type: "string",
        width: "10%",
        visible: false,
      },
      {
        property: "status",
        label: this.literals.columnsOfSlotsLabelRefresh,
        type: "icon",
        width: "10%",
        sortable: false,
        icons: [
          {
            action: this.getSlotRefresh.bind(this),
            icon: "po-icon-refresh",
            tooltip: this.literals.columnsOfSlotsTooltipRefresh,
            value: "1",
          },
        ],
      },
      {
        property: "haveparam",
        label: this.literals.columnsOfSlotsLabelParam,
        type: "icon",
        width: "10%",
        sortable: false,
        icons: [
          {
            action: this.getSlotParam.bind(this),
            icon: "po-icon-parameters",
            tooltip: this.literals.columnsOfSlotsTooltipParam,
            value: "true",
          },
        ],
      },
      {
        property: "haveagraphic",
        label: this.literals.columnsOfSlotsLabelGraphic,
        type: "icon",
        width: "5%",
        sortable: false,
        icons: [
          {
            action: this.goToGraphic.bind(this),
            icon: "po-icon-chart-area",
            tooltip: this.literals.columnsOfSlotsTooltipGraphic,
            value: "true",
          },
        ],
      },
    ];

    this.getEnvironments();
  }

  getEnvironments(): void {
    let find: boolean = false;
    this.environments = JSON.parse(this.environmentsService.environments);
    this.environmentInUse = this.environmentsService.environmentInUse;
    this.environments.forEach((item) => {
      item.value == this.environmentInUse ? (find = true) : "";
    });
    if (!find && this.environments.length > 0) {
      this.environmentInUse = this.environments[0].value.toString();
    }
    this.getInformations();
  }

  getInformations(): void {
    this.dbaccessService
      .getInfosOfApis(this.environmentInUse)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (items) => {
          this.state = "on";
          this.itemsOfConnections = items["retSlotsInfos"].filter(
            filterItemsOfConnection
          );
          this.itemsOfConsumption = items["retSlotsInfos"].filter(
            filterItemsOfComsuption
          );
          this.itemsOfOthers =
            items["retSlotsInfos"].filter(filterItemsOfOthers);
          this.itemsOfOthers = this.itemsOfOthers.concat(
            items["retGenericInfos"].filter(filterItemsOfGenerics)
          );
          this.itemsOfOthers = this.itemsOfOthers.concat(
            items["retTcPingInfos"]
          );
          this.itemsOfCheckup =
            items["retCheckupInfos"].filter(filterOfCheckup);
          this.itemsDetailsOfCheckup = items["retCheckupInfos"].filter(
            filterDetailsOfCheckup
          );

          items["retSlotsInfos"]
            .filter(filterOfStatic)
            .map((m: Tcgetinfo) => this.handlingReturn(m.value, m.slot));
        },
        error: () => {
          this.state = "error";
          return of();
        },
      });
  }

  getSlotParam(row) {
    if (row.haveparam === "true") {
      this.modalTemplate = "input";
      this.modalInputLabel = oSlotsWithParams[row.slot];
      this.modalTitle = oSlotsWithParams[row.slot];
      this.slotNumber = row.slot;
      this.poModal.open();
    }
  }

  getSlotRefresh(row): void {
    if (row.slot === 430) {
      this.dbaccessService
        .getTcPingInfos(this.environmentInUse, row.slot, this.paramOfSlot)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (items) => {
            this.valueOfRow = items[0].value;
          },
          error: (error) => {
            console.error(error);
            return of();
          },
        });
    } else {
      this.dbaccessService
        .getSlotsInfos(this.environmentInUse, row.slot, this.paramOfSlot)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (items) => {
            this.valueOfRow = items[0].value;
          },
          error: (error) => {
            console.error(error);
            return of();
          },
        });
    }
    this.paramOfSlot = "";
  }

  handlingReturn(message: string = "", slot: number = 99): void {
    let aSlot1: Array<string> = [
      "",
      "DBApiBuild: ",
      "Configuration: ",
      "Architecture: ",
      "Api ",
    ];
    let aAuxSplit = [];
    let reSeparator = /\s-\s/;
    let reBreakLine = /\r\n|\r|\n|\sBuild\s/g;

    message = message.replace(reBreakLine, " ");
    if (slot === 1) {
      aAuxSplit = message.split(reSeparator);
      for (let i = 0; i < aAuxSplit.length; i++) {
        this.aDetails.push(aSlot1[i] + aAuxSplit[i]);
      }
    }
    if (slot === 21)
      this.aDetails.push(`DbAuditTrail: ${message === "0" ? "OFF" : "ON"}`);
    if (slot === 22) this.aDetails.push(`MaxStringSize: ${message}bytes`);
    if (slot === 25)
      this.aDetails.push(`DataReadOnly: ${message ? "ON" : "OFF"}`);
    if (slot === 26) this.aDetails.push(`ReadOnly: ${message ? "ON" : "OFF"}`);
  }

  goToGraphic(row: Tcgetinfo) {
    let valueInNumber: number = parseInt(row.value);
    this.titleOfGraphic = this.literals.titleOfGraphicCpuInUse;
    this.rangeOfGraphic = [
      {
        label: this.literals.rangeOfGraphicLabelUsedCpu,
        data: valueInNumber,
        tooltip: this.literals.rangeOfGraphicTooltipUsedCpu,
      },
      {
        label: this.literals.rangeOfGraphicLabelFreeCpu,
        data: 100 - valueInNumber,
        tooltip: this.literals.rangeOfGraphicTooltipFreeCpu,
      },
    ];
    this.poModal.open();
  }

  getDetailsOfCheckup(row) {
    this.modalTemplate = "detail";
    this.modalTitle = row.description;
    this.modalRow = this.itemsDetailsOfCheckup
      .filter((f) => f.slot === row.slot)
      .map((item) => [item.description, item.value]);
    this.poModal.open();
  }

  closeModal() {
    this.poModal.close();
  }

  ngOnDestroy(): void {
    this.destroy$.next(false);
    this.destroy$.unsubscribe();
  }
}
