import {
  Observable,
  tap,
  catchError,
  throwError,
  map,
  forkJoin,
  take,
} from "rxjs";
import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Tcgetinfo } from "../models/tcgetinfo";
import { Host } from "../../shared/components/host.component";

const urlBaseManager: string = "tlpp/managerdbaccess/";
const urlBaseTcGetInfo: string = "tlpp/dbaccesstcgetinfoapi/";
const urlBaseGetInfos: string = "tlpp/dbaccessinfosapi/";
const environments: string = "dbaccessenvs";
const connect: string = "dbaccessconnect";
const getSlotsReturn: string = "getslotreturn";
const getCheckupInfos: string = "gettccheckupinfos";
const getGenericInfos: string = "getgenericinfos";
const getTcPingInfos: string = "gettcpinginfos";

@Injectable({
  providedIn: "root",
})
export class DbaccessService {
  private cUriBase: string = "";

  constructor(private http: HttpClient, private host: Host) {
    this.cUriBase =
      this.host.getProtocol() +
      "//" +
      this.host.getHost() +
      ":" +
      this.host.getPort() +
      this.host.getPath();
  }

  dbAccessEnvironments(): Observable<any> {
    let sURL = urlBaseManager + environments;
    return this.http.get<any>(`${this.host.setUrlService(sURL)}`).pipe(
      take(1),
      catchError(err => {
        console.log(err);
        return throwError(() => err);
      })
    );
  }

  dbAccessConnect(): Observable<Tcgetinfo> {
    let sURL = urlBaseManager + connect;
    return this.http
      .get<Tcgetinfo>(`${this.host.setUrlService(sURL)}`)
      .pipe(tap(console.log), take(1));
  }

  getInfosOfApis(...parms): Observable<any> {
    return forkJoin({
      retSlotsInfos: this.getSlotsInfos(parms),
      retCheckupInfos: this.getCheckupInfos(parms),
      retGenericInfos: this.getGenericInfos(parms),
      retTcPingInfos: this.getTcPingInfos(parms),
    });
  }

  getSlotsInfos(...parms): Observable<Tcgetinfo> {
    let params = this.setParamsToRequisition(parms);
    let sURL = urlBaseTcGetInfo + getSlotsReturn;
    return this.http
      .get<Tcgetinfo>(`${this.host.setUrlService(sURL)}`, {
        params: params,
      })
      .pipe(
        tap(console.log),
        map(s => s.slots),
        take(1)
      );
  }

  private getCheckupInfos(...parms): Observable<Tcgetinfo> {
    let params = this.setParamsToRequisition(parms);
    let sURL = urlBaseGetInfos + getCheckupInfos;
    return this.http
      .get<Tcgetinfo>(`${this.host.setUrlService(sURL)}`, {
        params: params,
      })
      .pipe(
        tap(console.log),
        map(i => i.slots),
        take(1)
      );
  }

  private getGenericInfos(...parms): Observable<Tcgetinfo> {
    let params = this.setParamsToRequisition(parms);
    let sURL = urlBaseGetInfos + getGenericInfos;
    return this.http
      .get<Tcgetinfo>(`${this.host.setUrlService(sURL)}`, {
        params: params,
      })
      .pipe(
        tap(console.log),
        map(i => i.slots),
        take(1)
      );
  }

  getTcPingInfos(...parms): Observable<Tcgetinfo> {
    let params = this.setParamsToRequisition(parms);
    let sURL = urlBaseGetInfos + getTcPingInfos;
    return this.http
      .get<Tcgetinfo>(`${this.host.setUrlService(sURL)}`, {
        params: params,
      })
      .pipe(
        tap(console.log),
        map(i => i.slots),
        take(1)
      );
  }

  private setParamsToRequisition(parms) {
    let params = new HttpParams();
    parms[0] ? (params = params.append("_cenvironment", parms[0])) : undefined;
    parms[1] ? (params = params.append("_nslot", parms[1])) : undefined;
    parms[2] ? (params = params.append("_xparamofslot", parms[2])) : undefined;
    return params;
  }
}
