import { Injectable } from '@angular/core';
import { 
  HttpClient, 
  HttpResponse} from '@angular/common/http';
import { Host } from 'src/app/shared/components/host.component'; 
import { 
  Observable, 
  catchError, 
  throwError} from 'rxjs';

@Injectable({
  providedIn: "root",
})
export class AppserverService {
  private readonly cUriMetrics: string = '/appserver/metrics'; 
  private readonly cUriService : string = '/totvs/environment';
  private readonly cUriCheck: string = '/tlpp/healthCheck';

  private readonly cUriStartdate : string = '/startdate';
  private readonly cUriMemoryRam : string = '?memory_ram_total&memory_ram_free&memory_ram_used';
  private readonly cUriMemoryApp : string = '?memory_resident&memory_virtual';
  private readonly cUriPagingMemory : string = '?memory_swap_total&memory_swap_used&memory_swap_free';
  private readonly cUriThreadMemory : string = '?memory_threads_used';
  private readonly cUriStartPath : string = '?files_startpath';
  private readonly cUriUsers : string = '?users_connected';
  private readonly cUriPower : string = '?power_performance';

  public bApiOK: boolean = false;

  constructor(private http: HttpClient, private host: Host) { 

    this.healthCheck().subscribe( resp => {
      this.bApiOK = ( resp.status == 200 );
    });
  }

  healthCheck(): Observable<HttpResponse<any>>{
    return this.http
              .get<any>(
                `${this.host.setUrlService(this.cUriCheck)}`,
                { observe: 'response' }
              )
  }

  getEnvironment(){
    return this
    .http
    .get<any>(`${this.host.setUrlService(this.cUriService)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getStartdate(){
    let sURL = this.cUriMetrics + this.cUriStartdate;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getMemoryRAM(){
    let sURL = this.cUriMetrics + this.cUriMemoryRam;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getMemoryApp(){
    let sURL = this.cUriMetrics + this.cUriMemoryApp;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getPagingMemory(){
    let sURL = this.cUriMetrics + this.cUriPagingMemory;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getThreadMemory(){
    let sURL = this.cUriMetrics + this.cUriThreadMemory;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getStartPath(){
    let sURL = this.cUriMetrics + this.cUriStartPath;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getUsers(){
    let sURL = this.cUriMetrics + this.cUriUsers;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  getPower(){
    let sURL = this.cUriMetrics + this.cUriPower;
    return this
    .http
    .get<any>(`${this.host.setUrlService(sURL)}`)
    .pipe(
      catchError((err) => {
        console.error(err);
        return throwError(() => err);
      })
    );
  }

  
}