  sCollectedModal: string = this.sValueEmpty;
  modalItems: Array<any> = [];

  // Environment
  stateEnvironment: string = "";
  contentSubscribeStartDate: fields[];
  contentSubscribeEnv: fields[];
  oAppserverSubscribe: object = [];
  oTlppSubscribe: object = [];
  sStartAppserver: string = this.sValueEmpty;

  // High Performance
